.rt-hero-wrapper {
  @include margin-bottom-large;
  position: relative;
}

.rt-hero {
  position: relative;
  width: 100%;
  background-color: transparent;
  color: $white;
  @media (min-width: $break-desktop) {
    background-color: transparent;
    color: $black;
  }
}

.rt-hero__bg-image {
  position: relative;
  height: 60vw;
  min-height: 250px;
  max-height: 380px;

  @media (min-width: $break-desktop) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    height: unset;
    max-height: unset;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--darken {
    filter: brightness(0.6);
  }
}

.rt-hero--has-mobile-bg {
  .rt-hero__bg-image--desktop {
    display: none;
    @media (min-width: $break-desktop) {
      display: block;
    }
  }
  .rt-hero__bg-image--mobile {
    @media (min-width: $break-desktop) {
      display: none;
    }
  }
}

.rt-hero__bg-image-mobile {
  height: 55vw;
  min-height: 250px;
  max-height: 350px;
  @media (min-width: $break-desktop) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    height: unset;
    max-height: unset;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--darken {
    filter: brightness(0.6);
  }
}

.rt-hero__caption {
  @include font-size(14, 14);
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 10px;
  background-color: rgba($black, 0.4);
  color: $white;
  @media (min-width: $break-desktop) {
    @include font-size(16, 16);
    padding: 12px;
  }

  &--mobile {
    @media (min-width: $break-desktop) {
      display: none;
    }
  }

  &--desktop {
    display: none;

    @media (min-width: $break-desktop) {
      display: block;
    }
  }
}

.rt-hero__bg-video {
  position: relative;
  display: none;
  height: 60vw;
  min-height: 250px;
  max-height: 380px;
  @media (min-width: $break-desktop) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    display: block;
    height: unset;
    max-height: unset;
  }

  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--darken {
    filter: brightness(0.6);
  }
}

.rt-hero__content {
  max-width: 1190px;
  margin: 0 auto;
  padding: 25px 0 35px;
  padding-right: var(--wp--preset--spacing--30);
  padding-left: var(--wp--preset--spacing--30);

  @include content-width;

  @media (min-width: $break-desktop) {
    display: flex;

    align-items: flex-start;
    justify-content: space-between;
    min-height: 600px;

    padding: $space-60 0 $space-30;
  }

  @include background-color;
  @media (min-width: $break-desktop) {
    background-color: transparent;
  }
}

.rt-hero__content-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.rt-hero__content-logos-mobile {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: $space-20;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  @media (min-width: $break-desktop) {
    display: none;
  }
}

.rt-hero__content-logo-mobile {
  width: 25vw;
  min-width: 120px;
  color: $white;
  span {
    display: block;
    @include font-size(14, 16);
    margin-bottom: $space-10;
  }
  &:nth-child(2) {
    margin-left: $space-20;
  }

  @media (min-width: $break-tablet) {
    span {
      @include font-size(16, 18);
    }
  }
}

.rt-hero__content-logos-desktop {
  display: none;

  @media (min-width: $break-desktop) {
    display: flex;
  }

  align-items: flex-start;
  justify-content: center;

  @media (min-width: $break-desktop) {
    padding-left: $space-20;
  }
}

.rt-hero__content-logo-desktop {
  max-width: 260px;
  color: $white;
  span {
    display: block;
    margin-bottom: $space-10;
    font-weight: bold;
    @include font-size(15, 20);
  }
  &:nth-child(2) {
    margin-left: $space-10;
  }

  @media (min-width: $break-desktop-med) {
    span {
      margin-bottom: $space-10;

      @include font-size(18, 20);
    }
    &:nth-child(2) {
      margin-left: $space-20;
    }
  }
}

.rt-hero__title {
  max-width: 540px;
  margin-bottom: 15px;
  padding: 0;

  @extend .h1;

  @media (min-width: $break-desktop) {
    max-width: 720px;
    // max-width: unset;
    margin-bottom: 35px;
    padding-left: 10px;

    span {
      display: inline-block;
      background: $green;
      box-shadow: 10px 0 0 $green, -10px 0 0 $green;

      color: $white;
      &:first-child {
        padding-top: 10px;
      }
      &:nth-last-child(2) {
        padding-bottom: 10px;
      }
    }
  }
}

.rt-hero__title--mobile {
  @media (min-width: $break-desktop) {
    display: none;
  }
}
.rt-hero__title--desktop {
  display: none;
  @media (min-width: $break-desktop) {
    display: block;
  }
}

.rt-hero__subtitle {
  display: block;
  max-width: 440px;
  margin-bottom: 20px;
  padding-left: 5px;

  @include font-size(20, 24);

  @media (min-width: $break-desktop) {
    display: inline;
    max-width: 720px;
    @include font-size(40, 47);

    span {
      display: inline;
      padding: 0 4px 0;
      background: $green;
      outline: 5px solid $green;
      color: $white;
    }
  }
}

.rt-hero__button {
  @include button-white;

  @media (min-width: $break-desktop) {
    @include button;
  }

  @media (min-width: $break-desktop-med) {
    margin-top: 60px;
  }
  &--white {
    @include button-white;
  }
}

// Addditonal for no text bg
.rt-hero--no-text-bg {
  .rt-hero__title,
  .rt-hero__subtitle {
    @media (min-width: $break-desktop) {
      padding: 0;
    }

    span {
      background: transparent;
      outline: none;
      box-shadow: none;
    }
  }
}

// Addditonal for no image hero
.rt-hero--no-image {
  border-top: 1px solid $white;
  color: $white;

  // @include background-color;

  // .rt-hero__title,
  // .rt-hero__subtitle {
  //   @media (min-width: $break-desktop) {
  //     padding: 0;
  //   }

  //   span {
  //     background: transparent;
  //     outline: none;
  //     box-shadow: none;
  //   }
  // }

  .rt-hero__content {
    @media (min-width: $break-tablet) {
      padding-top: 60px;
    }
    @media (min-width: $break-desktop) {
      height: unset;
    }
  }
}

// Custom homepage hero with logos

.rt-hero__homepage-text {
  display: none;
}

.rt-homepage-hero {
  .rt-hero__bg-image {
    position: relative;

    .rt-hero__homepage-text {
      position: absolute;
      top: 15px;
      right: 0;
      display: block;
      padding: 20px 10px 10px 10px;
      font-style: 16px;
      text-align: right;

      @media (min-width: 600px) {
        top: 40px;
      }
      @media (min-width: $break-tablet) {
        top: 40px;
        font-size: 18px;
      }
    }
    .rt-hero__homepage-text-strapline {
      display: block;
      max-width: 440px;
      margin-bottom: 10px;
      font-weight: bold;
      text-align: right;
    }
    .rt-hero__homepage-text-tag {
      display: block;
      font-style: 14px;

      text-align: right;
    }

    @media (min-width: $break-desktop) {
      position: absolute;
    }
    // mobile -- mobile -- mobile -- mobile -- mobile --
    &:after,
    &:before {
      position: absolute;
      bottom: 20px;
      z-index: 1;
      padding: 3px;
      background-color: rgba($black, 0.4);
      background-repeat: no-repeat;
      background-size: contain;
      border-radius: 6px;
      font-weight: bold;
      content: '';
      @media (min-width: $break-desktop) {
        display: none;
      }
    }

    &:after {
      right: 20px;
      width: 140px;
      height: 80px;
      background-image: url('/wp-content/themes/rethink-block-theme/assets/src/images/logos/bec-logo--white.png');
      background-position: 0 24px;
      content: 'CO-ORGANISER';
    }

    &:before {
      right: 180px;
      width: 180px;
      height: 78px;
      background-image: url('/wp-content/themes/rethink-block-theme/assets/src/images/logos/enviro-events-logo--white.png');
      background-position: 0 34px;
      content: 'ORGANISER';
    }
  }

  .rt-hero {
    &:after,
    &:before {
      position: absolute;
      bottom: 20px;
      display: none;
      padding: 3px;
      background-color: rgba($black, 0.4);
      background-repeat: no-repeat;
      background-position: 0, 0;
      background-size: contain;
      border-radius: 6px;
      color: $white;
      font-weight: bold;
      content: '';
      @media (min-width: $break-desktop) {
        display: block;
      }
    }

    &:after {
      right: 20px;
      width: 190px;
      height: 100px;
      background-image: url('/wp-content/themes/rethink-block-theme/assets/src/images/logos/bec-logo--white.png');
      background-position: 0 28px;
      content: 'CO-ORGANISER';
    }

    &:before {
      right: 240px;
      width: 300px;
      height: 100px;
      background-image: url('/wp-content/themes/rethink-block-theme/assets/src/images/logos/enviro-events-logo--white.png');
      background-position: 0 34px;
      content: 'ORGANISER';
    }
  }
}

.blog {
  .rt-hero-wrapper {
    padding-top: 115px !important;
    @media (min-width: $break-desktop) {
      // padding-top: calc(165px + var(--wp--preset--spacing--30)) !important;
      padding-top: 163px !important;
    }
  }
}
